@use '../util' as *;


.product-banner__wrapper {
    height: 50vh;
    position: absolute;
    left: 0;
    width: 100%;
    height: 50vh;
    .product-banner__content {
        background-image: url('/media/images/banner-img-1.jpg');
        background-size: cover;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        height: 100%;
        .container-inner {
            display: grid;
            grid-template-rows: 2fr auto 2fr;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            max-width: rem(400);
            h2,
            p {
                margin: auto;
                color: var(--text-color);
            }
            p {
                padding: rem(30) 0;
            }
            button {
                margin: rem(20);
                outline: none;
            }
            button:active {
                transform: scale(1.02);
            }
            .product-banner-buy-button {
                background: transparent;
                border: rem(2) solid #000;
                cursor: pointer;
                display: inline-block;
                font-family: 'Inter', sans-serif;
                font-weight: 500;
                height: rem(40);
                margin: auto;
                padding: rem(10) rem(25);
                position: relative;
                text-transform: uppercase;
                transition: all 0.3s ease;
                width: rem(130);
            }
            .product-banner-buy-button {
                background: var(--background);
                color: var(--text-color);
                line-height: rem(42);
                padding: 0;
                border: none;
            }
            .product-banner-buy-button span {
                position: relative;
                display: block;
                width: 100%;
                height: 100%;
            }
            .product-banner-buy-button:before,
            .product-banner-buy-button:after {
                position: absolute;
                content: "";
                height: 0%;
                width: rem(1);
                background: var(--text-color);
            }
            .product-banner-buy-button:before {
               right: 0;
               top: 0;
               transition: all 500ms ease;
            }
            .product-banner-buy-button:after {
               left: 0;
               bottom: 0;
               transition: all 500ms ease;
            }
            .product-banner-buy-button:hover {
               color: var(--text-color);
               background: var(--background2);
            }
            .product-banner-buy-button:hover:before {
               transition: all 500ms ease;
               height: 100%;
            }
            .product-banner-buy-button:hover:after {
               transition: all 500ms ease;
               height: 100%;
            }
            .product-banner-buy-button span:before,
            .product-banner-buy-button span:after {
               position: absolute;
               content: "";
               background: var(--text-color);
            }
            .product-banner-buy-button span:before {
               left: 0;
               top: 0;
               width: 0%;
               height: rem(1);
               transition: all 500ms ease;
            }
            .product-banner-buy-button span:after {
                right: 0;
                bottom: 0;
                width: 0%;
                height: rem(1);
                transition: all 500ms ease;
            }
            .product-banner-buy-button span:hover:before {
                width: 100%;
            }
            .product-banner-buy-button span:hover:after {
                width: 100%;
            }
        }
    }
}

.home-wide-banner {
    height: 50vh;
}