@use '../util' as *;

.main__homepage {
    .main-content__precontent-list {
        width: 100%;
        display: flex;
        justify-content: center;
        padding: rem(25) rem(0);
        border-top: rem(1) solid var(--text-color);
        border-bottom: rem(1) solid var(--text-color);
        position: relative;
        // margin: rem(30) rem(0);
        ul {
            text-decoration: none;
            list-style: none;
            flex-direction: row;
            margin: auto;
            padding: 0;
            .precontent-items {
                font-size: rem(12);
                font-weight: bold;
                display: inline-block;
                justify-content: space-between;
                margin: rem(0) rem(20);
                i {
                    margin-right: rem(10);
                }

            }
            @include breakpoint-down(small) {
            }
        }
    }
    .section__wrapper {
        max-width: 90rem;
        margin: auto;
        // Section Area1 : BestSellers
        .section__area1 {
            margin: rem(50) rem(0);
            display: grid;
            grid-template-rows: auto 5fr;
            grid-template-columns: repeat(3, auto);
            position: relative;
            .section-category {
                grid-row: 1;
                grid-column: 2;
                h2 {
                    text-align: center;
                    text-transform: uppercase;
                    font-weight: normal;
                }
                ul {
                    list-style: none;
                    display: flex;
                    padding: 0;
                    width: 30%;
                    margin: auto;
                    li {
                        height: rem(3);
                        width: rem(40);
                        background: var(--text-color);
                        border: 2px solid var(--text-color);
                        margin: auto;
                        padding: 0;
                    }
                }
            }
            .card {
                grid-row: 2;
            }
        }
    }
}

.section {
    &__wrapper {
        max-width: 100%;
        //height: 1000px;
    }
}


